nav {
    height: 5rem;
    width: 100vw;
    background: var(--color-primary);
    display: grid;
    place-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
}

/* only shows on medium and small screens */
.nav__toggle-btn {
    display: none;
}

.nav__container {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.logo {
    width: 7rem;
    display: block;
}

.nav__links {
    display: flex;
    grid-gap: 3.5rem;
    gap: 3.5rem;
    align-items: center;
}

.nav__links a {
    transition: var(--transition);
}

.nav__links a:hover {
    color: var(--color-secondary);
}

.active-nav {
    position: relative;
}

.active-nav::after {
    content: '';
    display: block;
    width: 1.2rem;
    height: 1.2rem;
    background: var(--color-primary);
    position: absolute;
    left: calc(50% - 0.6rem);
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    margin-top: 0.9rem;
}



/* MEDIA QUERIES (medium and small screens */

@media screen and (max-width: 1024px) {
    .nav__toggle-btn {
        display: inline-block;
        background: transparent;
        font-size: 1.8rem;
        cursor: pointer;
    }

    .nav__toggle-btn svg {
        color: var(--color-gray-100);
    }

    .nav__links {
        position: absolute;
        top: 100%;
        right: 0;
        flex-direction: column;
        grid-gap: 0;
        gap: 0;
        -webkit-perspective: 400px;
                perspective: 400px;
    }

    .active-nav, .active-nav::after {
        display: none;
    }

    .nav__links li {
        height: 4rem;
        width: 100%;
        box-shadow: -2rem 2rem 5rem rgba(0, 0, 0, 0.5);
        -webkit-animation: navAnimation 600ms ease forwards;
                animation: navAnimation 600ms ease forwards;
        -webkit-transform: rotateX('90deg');
                transform: rotateX('90deg');
        opacity: 0;
        -webkit-transform-origin: top;
                transform-origin: top;
    }

    .nav__links li:nth-child(2) {
        -webkit-animation-delay: 200ms;
                animation-delay: 200ms;
    }

    .nav__links li:nth-child(3) {
        -webkit-animation-delay: 400ms;
                animation-delay: 400ms;
    }

    .nav__links li:nth-child(4) {
        -webkit-animation-delay: 600ms;
                animation-delay: 600ms;
    }

    .nav__links li:nth-child(5) {
        -webkit-animation-delay: 800ms;
                animation-delay: 800ms;
    }

    .nav__links li:nth-child(6) {
        -webkit-animation-delay: 1s;
                animation-delay: 1s;
    }

    .nav__links li:nth-child(7) {
        -webkit-animation-delay: 1.2s;
                animation-delay: 1.2s;
    }

    @-webkit-keyframes navAnimation {
        to {
            -webkit-transform: rotateX(0);
                    transform: rotateX(0);
            opacity: 1;
        }
    }

    @keyframes navAnimation {
        to {
            -webkit-transform: rotateX(0);
                    transform: rotateX(0);
            opacity: 1;
        }
    }



    .nav__links li a {
        background-color: var(--color-primary);
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        padding: 1rem 5rem 1rem 3rem;
    }

    .show__nav {
        display: flex;
    }

    .hide__nav {
        display: none;
    }
}
*, *::before, *::after {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
} 

:root {
    --primary-hue: 210;
    --gray-hue: 240;
    --color-primary: hsl(var(--primary-hue), 100%, 50%);
    --color-primary-variant: hsl(var(--primary-hue), 100%, 65%);
    --color-secondary: #ffb116;
    --color-gray-100: hsl(var(--gray-hue), 47%, 94%);
    --color-gray-200: hsl(var(--gray-hue), 26%, 75%);
    --color-gray-300: hsl(var(--gray-hue), 24%, 64%);
    --color-gray-400: hsl(var(--gray-hue), 16%, 41%);
    --color-gray-500: hsl(var(--gray-hue), 44%, 25%);
    --color-gray-600: hsl(var(--gray-hue), 48%, 15%);

    --container-width-lg: 80%;
    --container-width-md: 90%;

    --transition: all 500ms ease;
}

/* GENERAL STYLES */

body {
    font-family: 'Montserrat', sans-serif;
    color: hsl(240, 26%, 75%);
    color: var(--color-gray-200);
    line-height: 1.7;
    overflow-x: hidden;
    background: hsl(240, 48%, 15%) url(data:image/png;base64,);
    background: var(--color-gray-600) url(data:image/png;base64,);
}

.container {
    width: 80%;
    width: var(--container-width-lg);
    max-width: 1920px;
    margin-inline: auto;
}

h1, h2, h3, h4, h5 {
    line-height: 1.2;
    color: hsl(240, 47%, 94%);
    color: var(--color-gray-100);
}

h1 {
    font-size: 3rem;
}

h2 {
    font-size: 2rem;
}

a {
    color: hsl(240, 47%, 94%);
    color: var(--color-gray-100);
}

img {
    display: block;
    object-fit: cover;
    width: 100%;
}









.btn {
    color: hsl(240, 47%, 94%);
    color: var(--color-gray-100);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0 auto;
    padding: 0.9rem 2rem;
    background: hsl(210, 100%, 50%);
    background: var(--color-primary);
    border-radius: 1.5rem;
    transition: all 500ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #ffb116;
    background: var(--color-secondary);
    color: hsl(240, 48%, 15%);
    color: var(--color-gray-600);
}

.btn.lg {
    padding: 1.2rem 3rem;
    border-radius: 2rem;
    font-size: 1.1rem;
}

.btn.sm {
    padding: 0.4rem 1.2rem;
    font-size: 0.9rem;
}

.btn.sm:hover {
    background: hsl(240, 47%, 94%);
    background: var(--color-gray-100);
}












section {
    margin-top: 10rem;
}

.section__head {
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
}

.section__head span {
    background: hsl(240, 44%, 25%);
    background: var(--color-gray-500);
    padding: 0.8rem;
    border-radius: 1rem;
    color: #ffb116;
    color: var(--color-secondary);
    font-size: 1.5rem;
}











.card {
    background: hsl(240, 44%, 25%);
    background: var(--color-gray-500);
    border: 2px solid transparent;
    text-align: center;
    padding: 3rem 2rem;
    border-radius: 0 3rem;
    transition: all 500ms ease;
    transition: var(--transition);
}

.card:hover {
    background: transparent;
    background-color: hsl(240, 16%, 41%);
    background-color: var(--color-gray-400);
    cursor: default;
}

.card span {
    width: 3rem;
    height: 3rem;
    background: hsl(210, 100%, 50%);
    background: var(--color-primary);
    color: hsl(240, 47%, 94%);
    color: var(--color-gray-100);
    font-size: 1.5rem;
    padding: 0.8rem;
    display: grid;
    place-items: center;
    margin-bottom: 1.5rem;
    margin-inline: auto;
    border-radius: 1rem;
    transition: all 500ms ease;
    transition: var(--transition);
}

.card:hover span {
    background: #ffb116;
    background: var(--color-secondary);
    color: hsl(240, 48%, 15%);
    color: var(--color-gray-600);
}

.card small{
    margin-top: 1rem;
    display: block;
    font-weight: 300;
}








.header {
    margin-top: 5rem;
    height: 20rem;
    overflow: hidden;
    border-bottom: 2px solid hsl(240, 16%, 41%);
    border-bottom: 2px solid var(--color-gray-400);
}

.header__container {
    width: 100%;
    height: 100%;
    position: relative;
    display: grid;
    place-items: center;
    background: black;
}

.header__container-bg {
    position: absolute;
    width: 100%;
}

.header__container-bg img {
    opacity: 0.5;
}

.header__content {
    position: relative;
    width: 44%;
    margin: 0 auto;
    text-align: center;
    color: hsl(240, 47%, 94%);
    color: var(--color-gray-100);
}

.header__content h2 {
    margin-bottom: 1rem;
}

.header__content p {
    color: rgba(255, 255, 255, 0.75);
} 










/* MEDIA QUERIES (medium screen) */
@media screen and (max-width: 
1024px) {
   .container {
    width: 90%;
    width: var(--container-width-md);
   } 

   h1 {
    font-size: 2rem;
   }

   h2 {
    font-size: 1.6rem;
   }

   .header__content {
    width: 76%;
   }
}




/* MEDIA QURIES (small screens) */
@media screen and (max-width: 
600px) {
    h1 {
        font-size: 2.2rem;
        line-height: 1.3;
    }

    section {
        margin-top: 7rem;
    }

    .header__content {
        width: 90%;
        width: var(--container-width-md);
        padding: 3rem 0;
    }

    .header__content p {
        font-size: 0.85rem;
    }
}
